import { useMemo, useState, useEffect } from 'react'
import Layout from '../components/Layout'
import toast from 'react-hot-toast'
import axios from 'axios'

// API
const api = axios.create({
  baseURL: "http://localhost:5000/api",
})

// Roles Ø«Ø§Ø¨ØªØ© Ø­Ø³Ø¨ Ø§Ù„Ù€ DB
const ROLE_LABELS = {
  1: 'Ù…Ø¯ÙŠØ± Ø§Ù„Ù†Ø¸Ø§Ù…',
  2: 'ØµÙŠØ¯Ù„ÙŠ',
  3: 'ÙƒØ§Ø´ÙŠØ±',
}

export default function UsersPage() {
  const [users, setUsers] = useState([])
  const [search, setSearch] = useState('')
  const [showAddModal, setShowAddModal] = useState(false)

  // Ù†Ù…ÙˆØ°Ø¬ Ø¥Ø¶Ø§ÙØ© Ù…Ø³ØªØ®Ø¯Ù…
  const [newUser, setNewUser] = useState({
    name: '',
    username: '',
    password: '',
    role_id: 3,
  })

  // ØªØ­Ù…ÙŠÙ„ Ø§Ù„Ù…Ø³ØªØ®Ø¯Ù…ÙŠÙ†
  const loadUsers = async () => {
    try {
      const res = await api.get("/users")
      setUsers(res.data)
    } catch {
      toast.error("Ø®Ø·Ø£ ÙÙŠ ØªØ­Ù…ÙŠÙ„ Ø§Ù„Ù…Ø³ØªØ®Ø¯Ù…ÙŠÙ†")
    }
  }

  useEffect(() => {
    loadUsers()
  }, [])

  // ÙÙ„ØªØ±Ø©
  const filteredUsers = useMemo(() => {
    const q = search.trim().toLowerCase()
    if (!q) return users
    return users.filter(
      (u) =>
        u.name?.toLowerCase().includes(q) ||
        u.username?.toLowerCase().includes(q)
    )
  }, [users, search])

  // Ø¥Ø¶Ø§ÙØ© Ù…Ø³ØªØ®Ø¯Ù…
  const handleAddUser = async () => {
    if (!newUser.name || !newUser.username || !newUser.password) {
      toast.error("ÙŠØ±Ø¬Ù‰ Ø¥Ø¯Ø®Ø§Ù„ Ø¬Ù…ÙŠØ¹ Ø§Ù„Ø­Ù‚ÙˆÙ„")
      return
    }

    try {
      await api.post("/users", newUser)
      toast.success("ØªÙ… Ø¥Ø¶Ø§ÙØ© Ø§Ù„Ù…Ø³ØªØ®Ø¯Ù…")
      setShowAddModal(false)
      setNewUser({ name: '', username: '', password: '', role_id: 3 })
      loadUsers()
    } catch {
      toast.error("ÙØ´Ù„ Ø¥Ù†Ø´Ø§Ø¡ Ø§Ù„Ù…Ø³ØªØ®Ø¯Ù…")
    }
  }

  // ØªÙØ¹ÙŠÙ„ / ØªØ¹Ø·ÙŠÙ„
  const toggleActive = async (id) => {
    try {
      await api.patch(`/users/${id}/toggle`)
      loadUsers()
      toast.success("ØªÙ… ØªØ­Ø¯ÙŠØ« Ø§Ù„Ø­Ø§Ù„Ø©")
    } catch {
      toast.error("Ø®Ø·Ø£ ÙÙŠ ØªØºÙŠÙŠØ± Ø§Ù„Ø­Ø§Ù„Ø©")
    }
  }

  // Ø­Ø°Ù
  const deleteUser = async (id) => {
    if (!confirm("Ù‡Ù„ ØªØ±ÙŠØ¯ Ø­Ø°Ù Ù‡Ø°Ø§ Ø§Ù„Ù…Ø³ØªØ®Ø¯Ù…ØŸ")) return

    try {
      await api.delete(`/users/${id}`)
      setUsers(prev => prev.filter(u => u.id !== id))
      toast.success("ØªÙ… Ø§Ù„Ø­Ø°Ù")
    } catch {
      toast.error("Ø®Ø·Ø£ ÙÙŠ Ø§Ù„Ø­Ø°Ù")
    }
  }

  return (
    <Layout title="Ø¥Ø¯Ø§Ø±Ø© Ø§Ù„Ù…Ø³ØªØ®Ø¯Ù…ÙŠÙ†">
      <div dir="rtl" className="space-y-6">

        {/* Ø§Ù„Ø¨Ø­Ø« + Ø¥Ø¶Ø§ÙØ© */}
        <div className="flex gap-3 p-4 bg-white border rounded-lg">
          <input
            type="text"
            placeholder="Ø¨Ø­Ø« Ø¨Ø§Ù„Ø§Ø³Ù… Ø£Ùˆ Ø§Ø³Ù… Ø§Ù„Ù…Ø³ØªØ®Ø¯Ù…"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            className="flex-1 p-2 border rounded"
          />
          <button
            onClick={() => setShowAddModal(true)}
            className="px-4 py-2 text-white bg-green-600 rounded"
          >
            Ø¥Ø¶Ø§ÙØ© Ù…Ø³ØªØ®Ø¯Ù…
          </button>
        </div>

        {/* Ø¬Ø¯ÙˆÙ„ Ø§Ù„Ù…Ø³ØªØ®Ø¯Ù…ÙŠÙ† */}
        <div className="overflow-x-auto bg-white border rounded-lg">
          <table className="w-full text-sm text-right">
            <thead className="bg-gray-50">
              <tr>
                <th>#</th>
                <th>Ø§Ù„Ø§Ø³Ù…</th>
                <th>Ø§Ø³Ù… Ø§Ù„Ù…Ø³ØªØ®Ø¯Ù…</th>
                <th>Ø§Ù„Ø¯ÙˆØ±</th>
                <th>Ø§Ù„Ø­Ø§Ù„Ø©</th>
                <th>Ø¥Ø¬Ø±Ø§Ø¡Ø§Øª</th>
              </tr>
            </thead>
            <tbody>
              {filteredUsers.map((u, i) => (
                <tr key={u.id} className="border-t">
                  <td className="p-2">{i + 1}</td>
                  <td className="p-2">{u.name}</td>
                  <td className="p-2">{u.username}</td>
                  <td className="p-2">{ROLE_LABELS[u.role_id]}</td>
                  <td className="p-2">
                    {u.active ? 'Ù…ÙØ¹Ù„' : 'Ù…ÙˆÙ‚ÙˆÙ'}
                  </td>
                  <td className="flex gap-2 p-2">
                    <button
                      onClick={() => toggleActive(u.id)}
                      className="px-2 py-1 bg-yellow-100 rounded"
                    >
                      Ø­Ø§Ù„Ø©
                    </button>
                    <button
                      onClick={() => deleteUser(u.id)}
                      className="px-2 py-1 bg-red-100 rounded"
                    >
                      Ø­Ø°Ù
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {/* Ù…ÙˆØ¯Ø§Ù„ Ø¥Ø¶Ø§ÙØ© */}
        {showAddModal && (
          <div className="fixed inset-0 flex items-center justify-center bg-black/40">
            <div className="w-full max-w-md p-6 bg-white rounded">
              <h2 className="mb-3 font-bold">Ø¥Ø¶Ø§ÙØ© Ù…Ø³ØªØ®Ø¯Ù…</h2>

              <Field label="Ø§Ù„Ø§Ø³Ù…">
                <input
                  className="w-full p-2 border rounded"
                  value={newUser.name}
                  onChange={(e) => setNewUser({ ...newUser, name: e.target.value })}
                />
              </Field>

              <Field label="Ø§Ø³Ù… Ø§Ù„Ù…Ø³ØªØ®Ø¯Ù…">
                <input
                  className="w-full p-2 border rounded"
                  value={newUser.username}
                  onChange={(e) => setNewUser({ ...newUser, username: e.target.value })}
                />
              </Field>

              <Field label="ÙƒÙ„Ù…Ø© Ø§Ù„Ù…Ø±ÙˆØ±">
                <input
                  type="password"
                  className="w-full p-2 border rounded"
                  value={newUser.password}
                  onChange={(e) => setNewUser({ ...newUser, password: e.target.value })}
                />
              </Field>

              <Field label="Ø§Ù„Ø¯ÙˆØ±">
                <select
                  className="w-full p-2 border rounded"
                  value={newUser.role_id}
                  onChange={(e) => setNewUser({ ...newUser, role_id: Number(e.target.value) })}
                >
                  <option value={1}>Ù…Ø¯ÙŠØ± Ø§Ù„Ù†Ø¸Ø§Ù…</option>
                  <option value={2}>ØµÙŠØ¯Ù„ÙŠ</option>
                  <option value={3}>ÙƒØ§Ø´ÙŠØ±</option>
                </select>
              </Field>

              <div className="flex justify-end gap-2 mt-4">
                <button onClick={() => setShowAddModal(false)}>Ø¥Ù„ØºØ§Ø¡</button>
                <button onClick={handleAddUser} className="px-4 py-2 text-white bg-green-600 rounded">
                  Ø­ÙØ¸
                </button>
              </div>
            </div>
          </div>
        )}

      </div>
    </Layout>
  )
}

function Field({ label, children }) {
  return (
    <div className="mb-3">
      <label className="block mb-1 text-xs text-gray-600">{label}</label>
      {children}
    </div>
  )
}









